import axios from 'axios'
import React, { useState } from 'react'
import { useLocation, useNavigate } from 'react-router-dom'
import styled from 'styled-components'
import { editRoute } from '../utils/APIroutes'

const Edit = () => {
    
    const navigate = useNavigate();
    
    const location = useLocation()
    var { i } = location.state
    const [values, setValues] = useState({
        Item:i.Item,
        Packaging:i.Packaging,
        Unit:i.Unit,
        Quantity:i.Quantity
      });

      const handleChange = (event) => {
        setValues({ ...values, [event.target.name]: event.target.value });
        console.log(values);
      };

    const handleClick= ()=>{
        const data = axios.put(editRoute,{values});
        if(data.status===false)
      {
        alert(data.message);
      }
      else{
        alert("Updated successfully!");
        navigate('/')
      }
    };
  return (
    <Container>
      <h1>Stock-Manager</h1>
      <div className="align">
        <div className="image">
            <img src="edit.png" alt="" />
        </div>
            <div className="edit-form">
                <h2 style={{textAlign:"center", margin:"1rem 0"}}>Edit Items</h2>
                <div className="entry">
                <div className="label">Description</div>
                <input name="Item" type="text" value={values.Item} disabled/>
                </div>
                <div className="entry">
                <div className="label">Unit</div>
                <input name="Unit" type="text" placeholder='Enter Unit' onChange={(e)=>handleChange(e)}/>
                </div>
                <div className="entry">
                <div className="label"> Packaging</div>
                <input name="Packaging" type="text" placeholder='Enter packaging' onChange={(e)=>handleChange(e)}/>
                </div>
                <div className="entry">
                <div className="label">Quantity</div>
                <input name="Quantity" type="number" placeholder='Enter Quantity' onChange={(e)=>handleChange(e)}/>
                </div>
                <div className="submit">
                    <input type="button" onClick={handleClick}value="Submit" />
                </div>
            </div>
      </div>
      </Container>
  )
}

const Container = styled.div`
font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
overflow-x:hidden;
color:white;
h1{
  width: 100vw;
  text-align: center;
  margin:3rem auto;
  font-family:Sans-serif; 
  color:blue;
  text-shadow:1px 1px 2px green; 
  font-size:2.5rem;
}
.align{
  display: flex;
  align-items: center;
  justify-content: center;
  .image{
    width:30vw;
    height:30rem;
    margin-right: 2rem;
    overflow: hidden;
    img{
        width: 100%;
    }
  }
  .edit-form{
    background-color:#6129da;
    padding: 7rem 5rem;
    display: flex;
    flex-direction: column;
    .entry{
        display: grid;
        font-size: 1.5rem;
        grid-template-columns: 1fr 3fr;
    align-items: center;
    input{
        margin:1rem 1rem;
        font-size: 1rem;
        height: 2rem;
    }
}
.submit{
    margin-top: 1rem;
    display:flex;
    justify-content: center;
    input{
        width:8rem;
        height: 3rem;
        font-size: 1rem;
        cursor: pointer;
        overflow: hidden;
        color: white;
        background-color: #1b1b94;
    }
    input:hover{
    color: white;
    box-shadow: 1px 2px 2px black;
    background-color: #1616abc5;
  }
}
  }
}
@media screen and (max-width: 800px) {
    .image{
        display: none;
    }
        img{
            display: none;
        }
    .align{
        .edit-form{
            padding: 6rem ;
            .entry{
                display: grid;
                font-size: 1.2rem;
                grid-template-columns: 1fr 3fr;
                align-items: center;
            }
        }
    }
}
`
export default Edit