import React, { useEffect, useState } from "react";
import styled from "styled-components";
import PersonIcon from "@mui/icons-material/Person";
import LockIcon from "@mui/icons-material/Lock";
import { loginRoute } from "../utils/APIroutes";
import axios from 'axios';
import { useNavigate } from "react-router-dom";


const Login = () => {
  const [values, setValues] = useState({
    username: "",
    password: "",
  });

  const navigate = useNavigate();

  const handleChange = (event) => {
    setValues({ ...values, [event.target.name]: event.target.value });
    console.log(values);
  };


  const loginAuth = async(e)=>{
    e.preventDefault();
    const { password, username} = values;
      const {data} = await axios.post(loginRoute,{
        username,
        password
      });

      if(data.status===false)
      {
        alert(data.message);
      }
      if(data.status===true){
        localStorage.setItem("logged in", "logged in");
        navigate("/"); 
      }
  }

  useEffect(() => {
    if (localStorage.getItem("logged in")) {
      navigate("/");
    }
  }, []);
  return (
    <Container>
        <h1 style={{fontFamily:"Sans-serif", color:"blue",textShadow:"1px 1px 2px green", fontSize:"2.5rem"}}>Stock-Manager</h1>
        <div className="cont">
      <div className="box">
        <div className="image">
          <img src="welcome.png" alt="" />
        </div>
        <div className="login-section">
          <div className="form-div">
            <div className="username-div">
              <div className="us-icon">
                <PersonIcon style={{color:"white" , fontSize: "2rem" }} />
              </div>
                <input className="us-inp"type="name" name="username" placeholder="Username" onChange={(e)=>handleChange(e)}/>
            </div>
            <div className="password-div">
              <div className="pass-icon">
                <LockIcon style={{color:"white" , fontSize: "2rem" }} />
              </div>
                <input className="pass-inp" type="password" name="password" placeholder="Password" onChange={(e)=>handleChange(e)}/>
            </div>

            <input onClick={loginAuth} className="submit" type="submit" value="Login" />
          </div>
        </div>
      </div>
    </div>
    </Container>
  );
};

const Container = styled.div`
  height: 100vh;
  width: 100vw;
  background-color: #f7f6f6;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  overflow-x: hidden;
  .cont{
  .box {
    width: 80vw;
    height: 80vh;
    /* background-color: bisque; */
    display: flex;
    .image {
      height: 100%;
      width: 50%;
      img {
        height: 100%;
        width: 100%;
      }
    }
    .login-section {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 50%;
      .form-div {
        padding: 10rem 8rem;
        background: linear-gradient(120deg,rgb(34, 97, 234),rgb(65, 28, 215));
          border:1px solid pink;
        .username-div {
            display: flex;
            width: 100%;
            margin-bottom: 2rem;
          .us-icon {
            display: inline;
            background-color: black;
          }
          .us-inp{
            height: 2.4rem;
            width: 13rem;
            outline: none;
          }
        }
        .password-div {
            display: flex;
            width: 100%;
            margin-bottom: 2rem;

          .pass-icon {
            display: inline;
            background-color: black;
          }
          .pass-inp{
            height: 2.4rem;
            width: 13rem;
            outline: none;
        }
        }
        .submit{
            width: 100%;
            font-size: 1.3rem;
            height: 3rem;
            color:white;
            font-weight: 500;
            letter-spacing: 1px;
            border-radius: 5px;
            border: 1px solid transparent;
            outline: none;
            cursor: pointer;
            overflow: hidden;
            border: 2px solid blue;
            color: #1b1b94;
            background-color: white;
        }
        .submit:hover{
            /* border: 2px solid white; */
            color: white;
            box-shadow: 1px 2px 2px black;
            background-color: #000000;
        }
      }
    }
  }
}
@media screen and (max-width:800px){
      .image{
        display: none;
      }
      .box{
        /* width: 100vw; */
        .login-section{
          margin:0 auto;
          .form-div{
            padding: 10rem;
          }
        }
      }
}
`;
export default Login;
