import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import { Link, redirect, useNavigate} from 'react-router-dom';
import { deleteRoute, getRoute } from '../utils/APIroutes';
import axios from 'axios';

const Home = () => {
    const [items,setItems]= useState([]);
    const navigate = useNavigate();
    useEffect(() => {
      return async ()=>{
        const checkStorage = localStorage.getItem("logged in");
      if (checkStorage!==null) {
            const data = await axios.get(getRoute);
            setItems(data.data);
    }
  else{
    navigate('/login');
    // eslint-disable-next-line no-restricted-globals
    location.reload();
  }}});
  return (
    <Container>
      <h1>Stock-Manager</h1>
      <div className="align">

      <div className="create-div">
        <Link to="/create">
        <button className="create">ADD NEW ITEM</button>
        </Link>
      </div>
      <div className="items-div">
        <div className="header-div">
          <div className="desc">Description</div>
          <div className="unit">Unit</div>
          <div className="pack">Packaging</div>
          <div className="quantity">Quantity</div>
          <div className="options">Options</div>
        </div>
        <div className="items">
          {
            items.map((i)=>{
              return (
                <form className="form-div" name={i.Item} action="">
                  <input type="text" className='desc' value={i.Item} disabled/>
                  <input type="text" className="unit" value={i.Unit} disabled />
                  <input type="text" className="pack" value={i.Packaging} disabled />
                  <input type="text" className="quantity" value={i.Quantity} disabled />
                  <div className="options">
                  <Link to="/editItem" state={{i}}>
                    <button className="edit-btn">
                      <EditIcon></EditIcon>
                    </button>
                    </Link>
                    <button onClick={()=>{
                      axios.post(deleteRoute,{Item:i.Item,Unit:i.Unit,Packaging:i.Packaging,Quantity:i.Quantity});
                      redirect('/');
                  }} className="delete-btn">
                      <DeleteIcon></DeleteIcon>
                    </button>
                  </div>
                </form>
              )
            })
          }
        </div>
        </div>
      </div>
    </Container>
  )
}

const Container = styled.div`
font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
overflow-x: hidden;
h1{
  width: 100vw;
  text-align: center;
  margin:3rem auto;
  font-family:Sans-serif; 
  color:blue;
  text-shadow:1px 1px 2px green; 
  font-size:2.5rem;
}
.align{
  display: flex;
  /* align-items:center; */
  justify-content: center;
  flex-direction: column;
}
.create-div{
  width: 80vw;
  display: flex;
  align-items: baseline;
  justify-content: end;
  .create{
    font-size: 1rem;
    padding: 0.5rem;
    /* height: 3rem; */
    font-weight: 500;
    letter-spacing: 1px;
    border-radius: 5px;
    outline: none;
    cursor: pointer;
    overflow: hidden;
    color: white;
    background-color: #1b1b94;
  }
  .create:hover{
    color: white;
    box-shadow: 1px 2px 2px black;
    background-color: #1616abc5;
  }
}
.items-div{
    width: 80vw;
    margin: 2rem 0;
    /* background-color: red; */
    .header-div{
      width: 100%;
      display: flex;
      height: 2rem;
      align-items: center;
      padding: 1rem;
      .desc{
        text-align: center;
        width: 40%;
        /* border: 1px solid black; */
        font-size: 1.3rem;
      }
      .unit,.pack,.quantity,.options{
        text-align: center;
        width: 15%;
        /* border: 1px solid black; */
        font-size: 1.3rem;
      }
    }
    .form-div{
      margin: 2rem 0;
      width: 100%;
      display: flex;
      justify-content: space-evenly;
      .desc{
        /* background-color: wheat; */
        text-align: center;
        width: 40%;
        /* border: 1px solid black; */
        font-size: 1.3rem;
      }
      .unit,.pack,.quantity,.options{
        text-align: center;
        /* background-color: wheat; */
        width: 15%;
        outline: none;
        /* border: 1px solid black; */
        padding:0.3rem;
        font-size: 1.3rem;
      }
      input:disabled{
        border: none;
      }
      .options{
        display: flex;
        justify-content: space-around;
        .edit-btn,.delete-btn{
          padding: 3px;
          border-radius: 5px;
          background-color: white;
          border: 1px solid blueviolet;
          color: #1b1b94;
          border:1px solid black;
        }
        .edit-btn:hover,.delete-btn:hover{
          box-shadow: 1px 2px 2px black;
          cursor: pointer;
        }
      }
    }
  }

  @media screen and (max-width: 800px) {
    .create-div{
      width: 100vw;
      align-items: center;
      justify-content: center;
    }
    .items-div{
      width: 90vw;
      margin:3rem 1rem;
      .header-div{
        .desc{
          width:50%;
          font-size: 1.1rem;
        }
        .pack,.unit{
          display: none;
        }
        .quantity,.options{
          width: 25%;
          font-size: 1.1rem;
        }
      }
      .form-div{
        .desc{
          width:50%;
          font-size: 1.1rem;
        }
        .pack,.unit{
          display: none;
        }
        .quantity,.options{
          width: 25%;
          font-size: 1.1rem;
        }
        .options{
        display: flex;
        justify-content: space-around;
        .edit-btn,.delete-btn{
          padding: 2px;
          border-radius: 5px;
          background-color: white;
          border: 1px solid blueviolet;
          color: #1b1b94;
          border:1px solid black;
        }
        .edit-btn:hover,.delete-btn:hover{
          box-shadow: 1px 2px 2px black;
          cursor: pointer;
        }
      }
      }
    }
  }
`;

export default Home